.container {
    height: 400px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: #00021A;
    color: #fafafa;
    padding: 30px;
    position: relative;
}

.nav {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    z-index: 1000;
}

.picture {
    position: absolute;
    right: 400px;
}

.blurb {
    position: absolute;
    left: 30px;
    max-width: calc(100vw - 500px);
}

.title {
    position: absolute;
    right: 300px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}


.photo {
    position: relative;
}

.photo img {
    position: absolute;
    top: 0;
    left: 0;
}


