.container {
    width: 300px;
    height: 300px;
    margin: 10px;
}

/* Card */
.card {
    width: 100%;
    height: 100%;
    -webkit-transition: -webkit-transform 200ms ease-in-out;
    transition: -webkit-transform 200ms ease-in-out;
    -o-transition: transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
    position: relative;
    color: #FAFAFA;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.card:hover {
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
}

.image {
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-filter: opacity(0.2);
            filter: opacity(0.2);
    -webkit-transition: -webkit-filter 200ms ease-in-out;
    transition: -webkit-filter 200ms ease-in-out;
    -o-transition: filter 200ms ease-in-out;
    transition: filter 200ms ease-in-out;
    transition: filter 200ms ease-in-out, -webkit-filter 200ms ease-in-out;
}

.card:hover .image{
    -webkit-filter: opacity(0.1);
            filter: opacity(0.1);
}

.titles {
    height: 100px;
    width: 200px;
    position: absolute;
    top: 100px;
    left: 50px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 10px;
    -webkit-transition: top 200ms ease-in-out, background-color 200ms ease-in-out;
    -o-transition: top 200ms ease-in-out, background-color 200ms ease-in-out;
    transition: top 200ms ease-in-out, background-color 200ms ease-in-out;
    text-align: center;
    background-color: inherit;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    text-transform: uppercase;
}

.title {
    font-size: 20px;
}

.subtitle {
    font-size: 14px;
}

.card:hover .titles {
    background-color: transparent;
    top: 50px;
}

.summary {
    position: absolute;
    top: 120px;
    opacity: 0;
    width: 100%;
    font-size: 14px;
    padding: 20px;
    text-align: center;
    -webkit-transition: top 200ms ease-in-out, opacity 200ms ease-in-out;
    -o-transition: top 200ms ease-in-out, opacity 200ms ease-in-out;
    transition: top 200ms ease-in-out, opacity 200ms ease-in-out;
}

.card:hover .summary {
    position: absolute;
    top: 140px;
    opacity: 1;
}

.tags {
    position: absolute;
    bottom: 0;
    padding: 10px;
    font-size: 14px;
}

/* Overlay */
.overlay {
    position: fixed; 
    overflow-y: scroll;
    top: 0; 
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0,0,0,0.9);
    z-index: 10000;
    -webkit-animation: fadein 300ms ease-in-out;
            animation: fadein 300ms ease-in-out;
}

.overlay .inner {
    position: absolute;
    top: 5vh;
    left: 5vh;
    min-height: 90vh;
    width: calc(100vw - 10vh);
    background: white;
    color: #222;
}

.closing {
    -webkit-animation: fadeout 300ms ease-in-out;
            animation: fadeout 300ms ease-in-out;
}

@-webkit-keyframes fadein {
    0% {opacity: 0;}
    0.01% {opacity: 0.01;}
    100% { opacity: 1; }
}

@keyframes fadein {
    0% {opacity: 0;}
    0.01% {opacity: 0.01;}
    100% { opacity: 1; }
}

@-webkit-keyframes fadeout {
    0% {opacity: 1;}
    0.01% {opacity: 0.99;}
    100% { opacity: 0; }
}

@keyframes fadeout {
    0% {opacity: 1;}
    0.01% {opacity: 0.99;}
    100% { opacity: 0; }
}

/* OVERLAY CONTENT */

.header {
    padding: 20px;
}

.carousel {
    height: 300px;
    overflow: hidden;
    overflow-x: scroll;
    display: -ms-flexbox;
    display: flex;
}

.carousel img {
    height: 100%;
}

.close {
    position: absolute;
    top: 0.5vh;
    right: 0.5vh;
    min-width: 4vh;
    min-height: 4vh;
}

.content {
    display: -ms-flexbox;
    display: flex;
    font-size: 14px;
    padding: 40px;
}

.left {
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 100%;
    padding-right: 20px;
    border-right: 1px solid #ddd;
}

.right {
    padding-left: 20px;
    width: 300px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

ul {
    list-style: none;
    padding-left: 10px;
}

li {
    margin-bottom: 5px;
}


